import Vue from 'vue'
import Router from 'vue-router'
import OrderForm from './views/OrderForm.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: '订单编辑',
      component: OrderForm,
    },
    {
      path: '/order-form',
      name: '订单编辑',
      component: () => import('./views/OrderForm.vue'),
    }
  ]
})
