<template>
  <div>
    <div class="user-info">
      <mt-field placeholder="收货信息" type="textarea" rows="3" v-model="row_info" class="form-textarea"></mt-field>
      <div class="splited-info" v-show="row_info">
        <mt-field placeholder="姓名" v-model="splited_info.user_name" class="form-input"></mt-field>
        <mt-field placeholder="电话" v-model="splited_info.user_phone" class="form-input"></mt-field>
        <mt-field placeholder="省" v-model="splited_info.porvince" class="form-input"></mt-field>
        <mt-field placeholder="市" v-model="splited_info.city" class="form-input"></mt-field>
        <mt-field placeholder="区" v-model="splited_info.country" class="form-input"></mt-field>
        <mt-field placeholder="街道" v-model="splited_info.street" type="textarea" class="form-input"></mt-field>
      </div>
    </div>
    <div class="item-list">
      <div class="item-box" v-for="(item,index) in items" :key="index">
        <div class="box-title">
          产品{{index + 1}}
          <div class="remove-btn" @click="removeItem(index)">移除</div>
        </div>
        <mt-field placeholder="产品名" v-model="item.name" class="form-input"></mt-field>
        <mt-field placeholder="分类" v-model="item.type" class="form-input"></mt-field>
        <mt-field placeholder="颜色" v-model="item.color" class="form-input"></mt-field>
        <mt-field placeholder="型号" v-model="item.size" class="form-input"></mt-field>
        <mt-field placeholder="数量" v-model="item.num" type="number" class="form-input"></mt-field>
        <mt-field placeholder="售价" v-model="item.price" type="number" class="form-input"></mt-field>
        <mt-field placeholder="取货价" v-model="item.cost" type="number" class="form-input"></mt-field>
        <mt-field placeholder="货号" v-model="item.sku_no" class="form-input"></mt-field>
        <mt-field placeholder="厂家" v-model="item.supplier" class="form-input"></mt-field>
        <mt-field placeholder="取货地址" v-model="item.address" class="form-input"></mt-field>
        <mt-field placeholder="备注" v-model="item.remark" class="form-input"></mt-field>
      </div>
      <div class="item-btn">
        <mt-button type="default" size="large" plain @click="addItem">添加产品</mt-button>
      </div>
    </div>
    <div>
      <mt-field placeholder="备注" v-model="remark" type="textarea" class="form-textarea"></mt-field>
    </div>
    <mt-button type="primary" size="large" plain @click="createOrder" class="submit-btn">创建订单</mt-button>
  </div>
</template>

<script>
import 'mint-ui/lib/style.css';
import { Field, Button } from 'mint-ui';

const wx = require('weixin-js-sdk');

class Item {
  constructor() {
    this.name = ''
    this.type = ''
    this.size = ''
    this.num = ''
    this.remark = ''
    this.price = ''
    this.sku_no = ''
    this.cost = ''
    this.color = ''
    this.supplier = ''
    this.address = ''
    this.images = []
  }
}

export default {
  name: 'OrderForm',
  components: {
    'mt-field': Field,
    'mt-button': Button,
  },
  data: () => {
    return {
      row_info: '',
      splited_info: {
        user_name: '',
        user_phone: '',
        porvince: '',
        city: '',
        country: '',
        street: '',
      },
      remark: '',
      items: [new Item()],
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    addItem () {
      this.items.push(new Item())
    },
    removeItem (index) {
      if(confirm('确认移除该产品吗？')) {
        this.items.splice(index, 1)
      }
    },
    createOrder () {
      if (this.splited_info.user_name && this.splited_info.user_phone) {
        if (confirm('确定创建订单吗？')) {
          const orderInfo = {
            ...this.splited_info,
            row_info: this.row_info,
            items: this.items.map(item => {
              return {
                ...item,
                price: item.price * 100,
                cost: item.cost * 100,
              }
            }),
            remark: this.remark,
          }
          let myHeaders = new Headers();
          myHeaders.append('Content-Type', 'application/json');
          fetch('/api/order/add', {
            method: 'POST',
            body: JSON.stringify(orderInfo),
            headers: myHeaders,
          })
          .then(res => res.json())
          .then(data => {
            console.log(data)
            wx.miniProgram.getEnv(ress => {
              if(ress.miniprogram) {
                wx.miniProgram.navigateTo({
                  url: `/pages/order/detail?id=${data._id}`
                })
              }
            })
          })
        }
      } else {
        alert('请填写收货信息')
      }
    }
  },
  watch: {
    row_info (val) {
      const splited_info = /^(\D*)?(\d{11})?(.*?省)?(.*?市)?(.*?区)?(.*)?$/g.exec(val)
      if (splited_info) {
        this.splited_info = {
          user_name: splited_info[1],
          user_phone: splited_info[2],
          porvince: splited_info[3],
          city: splited_info[4],
          country: splited_info[5],
          street: splited_info[6],
        }
      }
    }
  }
}
</script>

<style lang="scss">
.form-textarea {
  border: 1px solid #595959;
}
.form-input {
  border-bottom: 1px solid #595959;
}
.item-list {
  .item-box {
    margin-top: 10px;
    border-top: 5px solid #e8e8e8;
    .box-title {
      text-align: left;
      padding: 5px 10px;
      border-bottom: 1px solid #595959;
      .remove-btn {
        float: right;
        color: #f5222d;
      }
    }
  }
  .item-btn {
    margin-top: 10px;
    margin-bottom: 50px;
  }
}
.submit-btn {
  margin: 50px 0;
}
</style>


